@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-size: 16px;
}

* {
  box-sizing: border-box;
}

body {
  font-family: "Fredoka One", cursive;
  min-height: 100vh;
  max-width: 100vw;
  background-image: url("/battleboats/static/media/pink dust.9a75b78c.png");
}

.App {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
}

.board-container {
  display: grid;
}

@media (max-width: 575.9px) {
  html {
    font-size: 14px;
  }

  :root {
    --square: 25px;
    --game-size: 275px;
  }

  .board-container {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
  }
}

@media (min-width: 576px) {
  html {
    font-size: 14px;
  }

  :root {
    --square: 30px;
    --game-size: 330px;
  }

  .board-container {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
  }
}

@media (min-width: 768px) {
  html {
    font-size: 16px;
  }

  :root {
    --square: 40px;
    --game-size: 440px;
  }
}

@media (min-width: 992px) {
  .board-container {
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 1400px) {
  :root {
    --square: 50px;
    --game-size: 550px;
  }
}

.gameboard-container {
  display: grid;
  grid-template-columns: var(--square) 1fr;
  grid-template-rows: var(--square) 1fr;
  box-sizing: border-box;
}

.gameboard {
  border: 0.2rem solid #222;
  box-sizing: content-box;
  box-shadow: 0 8px 6px -6px black;
}

.gameboard-col-info {
  grid-column: 1/3;
  grid-row: 1/2;
  width: var(--game-size);
  display: flex;
  justify-content: space-between;
  color: #fff;
}

.gameboard-row-info .info-square {
  border: 0.1rem solid transparent;
}

.gameboard-row-info {
  grid-column: 1/2;
  grid-row: 1/3;
  height: var(--game-size);
  display: flex;
  flex-direction: column;
  color: #fff;
}

.info-square {
  width: var(--square);
  height: var(--square);
  display: flex;
  justify-content: center;
  align-items: center;
}

.gameboard {
  grid-column: 2/3;
  grid-row: 2/3;
  height: 400px;
  width: 400px;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
}

.gameboard-column {
  display: grid;
  grid-template-rows: repeat(10, 1fr);
  grid-template-columns: var(--square);
}

.board-square,
.board-hit,
.board-miss,
.is-sunk,
.your-square,
.your-ship {
  height: var(--square);
  width: var(--square);
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0.1rem solid #222;
  color: #fff;
  overflow: hidden;
  box-sizing: border-box;
}

.board-square,
.your-square {
  background-color: #888;
  transition: all 0.2s ease-in-out;
}

.your-square:hover {
  -webkit-filter: brightness(50%);
          filter: brightness(50%);
  cursor: crosshair;
}

.board-miss {
  background-color: #1976d2;
  font-size: 2.5rem;
}

.board-hit {
  background-color: #d32f2f;
  border: 0.4rem solid #222;
  font-size: 2rem;
}

.is-sunk {
  background-color: #555;
  border: 0.4rem solid #222;
  color: #222;
  font-size: 2rem;
}

.your-ship {
  background-color: #388e3c;
}

.your-info,
.pc-info {
  width: calc(100% - var(--square));
  margin: 0.5rem 0;
  margin-left: var(--square);
  border: 0.2rem solid #222;
  border-radius: 0.5rem;
  text-align: center;
  font-size: 1.2rem;
  line-height: 1.5;
  color: #fff;
  box-shadow: 0 8px 6px -6px black;
}

.your-info {
  background-color: #1b5e20;
}

.pc-info {
  background-color: #4a148c;
}

.active,
.inactive {
  padding: 1rem;
  border-radius: 0.5rem;
  border: 0.1rem solid transparent;
}

.active {
  animation: pulse 1.5s infinite ease-in-out;
}

@keyframes pulse {
  0% {
    background: transparent;
  }
  50% {
    background: #444;
    border: 0.1rem solid #888;
  }
  100% {
    background: transparent;
  }
}

@media (max-width: 575.9px) {
  .gameboard {
    height: 250px;
    width: 250px;
  }
}

@media (min-width: 576px) {
  .gameboard {
    height: 300px;
    width: 300px;
  }
}

@media (min-width: 768px) {
  .gameboard {
    height: 400px;
    width: 400px;
  }
}

@media (min-width: 992px) {
}
@media (min-width: 1400px) {
  .gameboard {
    height: 500px;
    width: 500px;
  }
}

.main-nav {
  width: 100%;
  max-width: 1400px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 3rem;
  background-color: #242526;
}

.nav-title {
  color: #fff;
  font-size: 1.4rem;
}

.nav-links {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  gap: 20px;
}

.nav-button {
  padding: 0.5rem;
  border: 0.2rem solid #222;
  border-radius: 0.5rem;
  background-color: #4a148c;
  color: #fff;
  font-size: 1.2rem;
  font-family: inherit;
  transition: all 0.2s ease-in-out;
}

.nav-button:hover {
  cursor: pointer;
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
  border: 0.2rem solid #fff;
}

.setup-container {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 20px;
  gap: 20px;
  padding: 1rem;
}

.setup-main-container {
  display: grid;
  align-content: center;
  justify-items: center;
}

.setup-button-box {
  display: flex;
  justify-content: space-around;
}

.start-button {
  padding: 1rem;
  border: 0.2rem solid #222;
  border-radius: 0.5rem;
  background-color: #388e3c;
  color: #fff;
  font-size: 1.2rem;
  font-family: inherit;
  transition: all 0.2s ease-in-out;
  box-shadow: 0 8px 6px -6px black;
  animation: grow 1s ease-in-out infinite;
}

.start-button:disabled {
  color: #aaa;
  -webkit-filter: brightness(50%);
          filter: brightness(50%);
  pointer-events: none;
  animation: none;
}

.start-button:hover {
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
  border: 0.2rem solid #fff;
  cursor: pointer;
}

.setup-button {
  padding: 1rem;
  border: 0.2rem solid #222;
  border-radius: 0.5rem;
  background-color: #512da8;
  color: #fff;
  font-size: 1.2rem;
  font-family: inherit;
  transition: all 0.2s ease-in-out;
  box-shadow: 0 8px 6px -6px black;
}

.setup-button:hover {
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
  border: 0.2rem solid #fff;
  cursor: pointer;
}

.setup-gameboard {
  position: relative;
}

#tip3 {
  top: 10px;
  right: -100px;
}

@keyframes grow {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

@media (max-width: 575.9px) {
  .setup-main-container {
    grid-template-columns: 1fr;
  }
}

@media (min-width: 576px) {
  .setup-main-container {
    grid-template-columns: 1fr;
  }
}

@media (min-width: 768px) {
}

@media (min-width: 992px) {
  .setup-main-container {
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 1200px) {
}

.boatyard {
  position: relative;
  place-self: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem 3rem;
  border: 0.2rem solid #222;
  border-radius: 0.5rem;
  background: #444;
  box-shadow: 0 8px 6px -6px black;
}

.boatyard-title {
  margin-bottom: 1rem;
  font-size: 1.8rem;
  font-weight: 700;
  color: #fff;
  text-align: center;
}

.floating-tip {
  position: absolute;
  padding: 1rem;
  border: 0.2rem solid #222;
  border-radius: 2rem;
  color: #fff;
  background-color: #333;
  box-shadow: 0 8px 6px -6px black;
}

#tip1 {
  top: 50px;
  left: -80px;
}

#tip2 {
  bottom: 10px;
  right: -100px;
}

.dock {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(6 * var(--square));
  width: calc(6 * var(--square));
  padding: 1rem;
  border: 0.2rem solid #222;
  border-radius: 0.5rem;
  background-color: #2874a6;
}

.dock-text {
  font-size: 1.4rem;
}

.boat-name {
  font-size: 1.2rem;
  color: #fff;
  text-align: center;
  margin-top: 1rem;
}

.boat {
  height: var(--square);
  width: var(--square);
  border: 0.1rem solid #333;
  background-color: #388e3c;
}

.boat-vertical,
.boat-horizontal {
  display: flex;
  animation: small-grow 1s ease-in-out infinite;
}

.boat-horizontal {
  flex-direction: row;
}

.boat-vertical {
  flex-direction: column;
}

.boat-vertical:hover,
.boat-horizontal:hover {
  cursor: -webkit-grab;
  cursor: grab;
}

@keyframes small-grow {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

.overlay {
  position: absolute;
  height: 100%;
  width: 100vw;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.5);
}

.win-box,
.lose-box {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  border: 0.2rem solid #222;
  border-radius: 0.5rem;
  z-index: 3;
}

.win-box {
  background-color: #1b5e20;
}

.lose-box {
  background-color: #4a148c;
}

.gameover-heading,
.gameover-message {
  color: #fff;
  text-align: center;
  margin: 1rem 0;
}

.gameover-heading {
  font-size: 2rem;
  font-weight: 700;
}

.gameover-message {
  font-size: 1.4rem;
}

.gameover-button {
  padding: 0.5rem;
  margin-top: 1rem;
  border: 0.2rem solid #222;
  border-radius: 0.5rem;
  background-color: #333;
  color: #fff;
  font-family: inherit;
  font-size: 1.2rem;
  transition: all 0.2s ease-in-out;
}

.gameover-button:hover {
  cursor: pointer;
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
  border: 0.2rem solid #fff;
}

.turn-indicator {
  place-self: center;
  margin-bottom: 5rem;
}

.turn-title {
  margin-bottom: 1rem;
  font-size: 1.4rem;
  font-weight: 700;
  color: #fff;
  text-align: center;
}

.arrow_box_right,
.arrow_box_left {
  width: 4rem;
  height: 4rem;
  border: 2px solid #222;
  border-radius: 50%;
  padding: 1rem;
  font-size: 2rem;
  color: #fff;
  text-align: center;
  box-shadow: 0 8px 6px -6px black;
}

.arrow_box_left {
  background-color: #4a148c;
}

.arrow_box_right {
  background-color: #1b5e20;
}

.rules-container {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 0.2rem solid #222;
  background-color: #333;
  color: #fff;
}

